.brand-wrapper {
	padding-bottom: 8px;

	.logo {
		height: 55px;
		cursor: pointer;
	}
}

.loading {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background-color: #b0adad4b;
	z-index: 100;
	text-align: center;
}

.login {
	&-section-wrapper {
		display: flex;
		flex-direction: column;
		padding: 48px 50px;
		background-color: #fff;

		@media (max-width: 991px) {
			padding-left: 50px;
			padding-right: 50px;
		}
		@media (max-width: 575px) {
			padding-top: 20px;
			padding-bottom: 20px;
			min-height: 100vh;
		}
	}

	&-wrapper {
		height: max-content;
		width: 300px;
		max-width: 100%;
		padding-top: 14px;
		padding-bottom: 24px;

		@media (max-width: 575px) {
			width: 100%;
		}

		label {
			font-size: 14px;
			font-weight: bold;
			color: #b0adad;
		}

		.form-control {
			border: none;
			border-bottom: 1px solid #e7e7e7;
			border-radius: 0;
			padding: 9px 5px;
			min-height: 40px;
			font-size: 18px;
			font-weight: normal;

			&::placeholder {
				color: #b0adad;
			}
		}

		.login-btn {
			padding: 13px 20px;
			background-color: #fdbb28;
			border-radius: 0;
			font-size: 20px;
			font-weight: bold;
			color: #fff;
			margin-bottom: 14px;
			border-radius: 30px;

			&:hover {
				border: 1px solid #fdbb28;
				background-color: #fff;
				color: #fdbb28;
			}
		}

		a.forgot-password-link {
			color: #080808;
			font-size: 14px;
			text-decoration: underline;
			display: inline-block;
			margin-bottom: 54px;

			@media (max-width: 575px) {
				margin-bottom: 16px;
			}
		}

		&-footer-text {
			font-size: 16px;
			color: #000;
			margin-bottom: 0;
		}
	}

	&-title {
		font-size: 30px;
		color: #000;
		font-weight: bold;
		margin-bottom: 25px;
	}

	&-img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: left;
	}
}
