body {
	font-family: 'Karla', sans-serif;
	background-color: #d0d0ce1f;
	min-height: 100vh;
}

.brand-wrapper {
	margin-bottom: 19px;

	.logo {
		height: 60px;
		cursor: pointer;
	}
}

.close {
	position: absolute;
	z-index: 99;
	right: 0px;
	top: 0px;
}

.login-card {
	border: 0;
	border-radius: 27.5px;
	box-shadow: 0 10px 30px 0 rgba(172, 168, 168, 0.43);
	overflow: hidden;

	&-img {
		border-radius: 0;
		position: absolute;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	.card-body {
		padding: 85px 60px 60px;

		@media (max-width: 422px) {
			padding: 35px 24px;
		}
	}

	&-description {
		font-size: 25px;
		color: #000;
		font-weight: normal;
		margin-bottom: 23px;
	}

	form {
		max-width: 326px;
	}

	.text-reset {
		cursor: pointer;
		padding: 13px 20px 12px;
		background-color: #000;
		border-radius: 4px;
		font-size: 17px;
		font-weight: bold;
		line-height: 20px;
		color: #fff !important;
		margin-bottom: 24px;
		border-radius: 30px;

		&:hover {
			border: 1px solid #000;
			background-color: transparent;
			color: #000;
		}
	}

	.form-control {
		border: 1px solid #d5dae2;
		padding: 15px 25px;
		margin-bottom: 20px;
		min-height: 45px;
		font-size: 13px;
		line-height: 15;
		font-weight: normal;

		&::placeholder {
			color: #919aa3;
		}
	}

	.login-btn {
		padding: 13px 20px 12px;
		background-color: #000;
		border-radius: 4px;
		font-size: 17px;
		font-weight: bold;
		line-height: 20px;
		color: #fff;
		margin-bottom: 24px;

		&:hover {
			border: 1px solid #000;
			background-color: transparent;
			color: #000;
		}
	}

	.forgot-password-link {
		font-size: 14px;
		color: #919aa3;
		margin-bottom: 12px;
	}

	&-footer-text {
		font-size: 16px;
		color: #0d2366;
		margin-bottom: 60px;

		@media (max-width: 767px) {
			margin-bottom: 24px;
		}
	}

	&-footer-nav {
		a {
			font-size: 14px;
			color: #919aa3;
		}
	}
}
